/*culture specific styles should go here*/

@import '_settings-en-US';

@font-face {
    font-family: 'noto_sansregular';
    src: url('/fonts/notosans-en/NotoSans-Regular-webfont.eot');
    src: url('/fonts/notosans-en/NotoSans-Regular-webfont.eot?#iefix') format('embedded-opentype'),
        url('/fonts/notosans-en/NotoSans-Regular-webfont.woff') format('woff'),
        url('/fonts/notosans-en/NotoSans-Regular-webfont.ttf') format('truetype'),
        url('/fonts/notosans-en/NotoSans-Regular-webfont.svg#noto_sansregular') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'noto_sansregular';
    src: url('/fonts/notosans-en/NotoSans-Bold-webfont.eot');
    src: url('/fonts/notosans-en/NotoSans-Bold-webfont.eot?#iefix') format('embedded-opentype'),
        url('/fonts/notosans-en/NotoSans-Bold-webfont.woff') format('woff'),
        url('/fonts/notosans-en/NotoSans-Bold-webfont.ttf') format('truetype'),
        url('/fonts/notosans-en/NotoSans-Bold-webfont.svg#noto_sansbold') format('svg');
    font-weight: bold;
    font-style: normal;
}

html{
    direction: $direction;
}
body{
    font-family: $font-family;
    line-height: $lineheight;
    font-size: $fsize-normal;
}


//----------------------------------------------------
//ovverrides: forms
.form-group{
    margin-bottom: ($lineheight / 2);
}
label{
    margin-bottom: 0;font-weight: normal;
}
.form-control{
    height: ($lineheight * 1.5);padding: 0 1rem;line-height: ($lineheight * 1.5);font-size: $fsize-sm;
}

.form-lg{
    label{
        font-size: $fsize-md;
}
    .form-control{
        height: ($lineheight * 2);line-height: ($lineheight * 2);
    }
}

//----------------------------------------------------
//ovverrides: buttons
.btn{
    font-size: $fsize-normal;line-height: $lineheight;padding: ($lineheight / 6) .9rem (($lineheight / 6) + .1rem);font-family: $font-family;
    &.btn-lg{
        font-size: $fsize-lg;padding: (($lineheight / 2) - .1rem) .9rem;
    }
    &.btn-sm{
        font-size: $fsize-sm;
    }
}


//----------------------------------------------------
.avatar-holder{
    float: $text-direction;
}
.header-tools{
    float: $text-reverse;
}


//----------------------------------------------------
.toolsarea-level1{
    .breadcrumb{
        float: $text-direction;
    }
}
.tools-level1{
    float: $text-reverse;
}